import React, { Component } from "react"
import ParticlesBg from "particles-bg"
import Fade from "react-awesome-reveal"

class Header extends Component {
  render() {
    return (
      <header id="home">
        <ParticlesBg type="cobweb" bg={true} color="#6495ED" />

        <nav id="nav-wrap">
          <a className="mobile-btn" href="#nav-wrap" title="Show navigation">
            Show navigation
          </a>
          <a className="mobile-btn" href="#home" title="Hide navigation">
            Hide navigation
          </a>

          <ul id="nav" className="nav">
            <li className="current">
              <a className="smoothscroll" href="#home">
                
              </a>
            </li>

            <li>
              <a className="smoothscroll" href="#about">
                
              </a>
            </li>

            <li>
              <a className="smoothscroll" href="#steps">
                
              </a>
            </li>

            <li>
              <a className="smoothscroll" href="#milestones">
                
              </a>
            </li>
            <li>
              <a className="smoothscroll" href="#reviews">
                
              </a>
            </li>

            <li>
              <a className="smoothscroll" href="#properties">
                
              </a>
            </li>
            <li>
              <a className="smoothscroll" href="#contact">
                
              </a>
            </li>
          </ul>
        </nav>

        <div className="row banner">
          <div id="logo-image"></div>

          <div className="banner-text">
            <Fade bottom triggerOnce>
              <h1 className="responsive-headline">Dai BA Service</h1>
            </Fade>
            <Fade bottom duration={1200} triggerOnce>
              <h2></h2>
            </Fade>
            <hr />
            <Fade bottom duration={2000} triggerOnce>
              <ul className="social">
                <li>
                  <a href="https://www.xiaohongshu.com/user/profile/5be1a497848b020001a0569b" className="button btn project-btn">
                    <i className="fa fa-book"></i>
                  </a>
                </li>
                <li>
                  <a href="https://www.youtube.com/@RyanDaiAU" className="button btn github-btn">
                    <i className="fa fa-youtube"></i>Youtube
                  </a>
                </li>
              </ul>
            </Fade>
          </div>
        </div>

        <p className="scrolldown">
          <a className="smoothscroll" href="#about">
            <i className="icon-down-circle"></i>
          </a>
        </p>
      </header>
    )
  }
}

export default Header
