import React, { Component } from "react"
import Slide from "react-awesome-reveal"

class Milestones extends Component {
  render() {
    return (
      <section id="milestones">
        <Slide left duration={1300} triggerOnce>
          <div className="row work">
            <div className="three columns header-col">
              <h1>
                <span></span>
              </h1>
            </div>
            <div className="nine columns main-col">
              <ul className="milestones">
                <li className="milestones-event">
                  <label className="milestones-event-icon"></label>
                  <div className="milestones-event-copy">
                    <div className="milestonesContainer">
                      <p className="milestones-event-thumbnail"></p>
                    </div>
                    <h4>house</h4>
                    <ul>
                      <li></li>
                      <li>townhouse</li>
                      <li>120w</li>
                    </ul>
                  </div>
                </li>
                <li className="milestones-event">
                  <label className="milestones-event-icon"></label>
                  <div className="milestones-event-copy">
                    <div className="milestonesContainer">
                      <p className="milestones-event-thumbnail"></p>
                    </div>
                    <h4></h4>
                    <ul>
                      <li></li>
                      <li></li>
                      <li></li>
                    </ul>
                  </div>
                </li>
                <li className="milestones-event">
                  <label className="milestones-event-icon"></label>
                  <div className="milestones-event-copy">
                    <div className="milestonesContainer">
                      <p className="milestones-event-thumbnail"></p>
                    </div>
                    <h4></h4>
                    <ul>
                      <li>onlineoff market</li>
                      <li></li>
                    </ul>
                  </div>
                </li>
                <li className="milestones-event">
                  <label className="milestones-event-icon"></label>
                  <div className="milestones-event-copy">
                    <div className="milestonesContainer">
                      <p className="milestones-event-thumbnail"></p>
                    </div>
                    <h4></h4>
                    <ul>
                      <li></li>
                      <li></li>
                    </ul>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </Slide>
      </section>
    )
  }
}

export default Milestones
